import "./styles.css";
import menu from "./car";

import * as React from "react";
import Button from "@mui/material/Button";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import PopupState, { bindTrigger, bindMenu } from "material-ui-popup-state";

import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

export default function App() {
  return (
    <React.Fragment>
      <PopupState variant="popover" popupId="demo-popup-menu">
        {(popupState) => (
          <React.Fragment>
            <Button variant="contained" fullWidth {...bindTrigger(popupState)}>
              メニュー
            </Button>
            <Menu {...bindMenu(popupState)}>
              <MenuItem onClick={popupState.close}>
                {menus(1)}
                {menus(0)}
              </MenuItem>
              <MenuItem onClick={popupState.close}>
                {menus(1)}
                {menus(1)}
              </MenuItem>
              <MenuItem onClick={popupState.close}>
                {menus(2)}
                {menus(2)}
              </MenuItem>
              <MenuItem onClick={popupState.close}>
                {menus(2)}
                {menus(2)}
              </MenuItem>
            </Menu>
          </React.Fragment>
        )}
      </PopupState>

      <CssBaseline />
      <Container>
        <Box sx={{ bgcolor: "#cfe8fc", height: "70vh", width: "100%" }} />
      </Container>
    </React.Fragment>
    /*
    <>
      <ul>
        <li>{menu[0]["種類"]}</li>
        <li>{menu[0]["menu"]}</li>
        <li>{menu[0]["値段"]}</li>
        <li>{menu[0]["写真"]}</li>
      </ul>
      <ul>
        <li>{menu[1]["種類"]}</li>
        <li>{menu[1]["menu"]}</li>
        <li>{menu[1]["値段"]}</li>
        <li>{menu[1]["写真"]}</li>
      </ul>
    </>*/
  );
}
